








































































import { Vue, Component } from 'vue-property-decorator';
import { CheckboxesData } from '@/types/views/components/checkboxes.interface';

@Component
export default class CheckboxesView extends Vue {
  textItems!: object[];
  checkboxesItems!: any[];

  data(): CheckboxesData {
    return {
      textItems: [
        {
          text: '',
          lists: [
            ' ',
            ' ',
            ' '
          ],
        },
        { text: '' }
      ],
      checkboxesItems: [
        {
          title: '',
          texts: [''],
          checkboxes: [
            {
              items: [
                { label: '' },
                { label: '' },
                { label: '' }
              ],
              selected: ['']
            }
          ]
        },
        {
          title: '',
          texts: [''],
          checkboxes: [
            {
              items: [
                { label: '', disabled: true },
                { label: '', disabled: true, indeterminate: true },
                { label: '', disabled: true }
              ],
              selected: ['']
            }
          ]
        },
        {
          title: '',
          texts: [
            '',
            ' ',
            ' ',
            ' '
          ],
          checkboxes: [
            {
              items: [
                { label: '' },
                { label: '' },
                { label: '' }
              ],
              selected: [''],
              parent: {
                label: ''
              }
            }
          ]
        },
        {
          title: '',
          texts: ['2'],
          checkboxes: [
            {
              items: [
                { label: '' },
                { label: '', indeterminate: true },
                { label: '' },
                { label: '', dense: true },
                { label: '', dense: true, indeterminate: true },
                { label: '', dense: true }
              ],
              selected: ['']
            }
          ]
        }
      ]
    };
  }
  get findCheckbox() {
    return this.checkboxesItems.find((element: any) => {
      return element.checkboxes[0].parent;
    }).checkboxes[0];
  }
  get selectedAll() {
    return this.findCheckbox.selected.length === this.findCheckbox.items.length
      ? true
      : false;
  }
  set selectedAll(val) {
    // if (!val && this.findCheckbox.selected.length !== this.findCheckbox.items.length) {
    if (!val) {
      this.findCheckbox.selected = [];
      // console.log(this.findCheckbox.selected);
    } else if (val) {
      // console.log(this.findCheckbox.selected);
      const selected = this.findCheckbox.items.map((element: any) => {
        return element.label;
      });
      this.findCheckbox.selected = selected;
      // console.log(this.findCheckbox.selected);
    }
  }
  get indeterminate() {
    return (checkbox: any) => {
      return 0 < checkbox.selected.length &&
        checkbox.selected.length < checkbox.items.length
        ? true
        : false;
    }
  }
}
